import React, { Suspense, useRef, useState, useEffect } from "react"
import { ContactShadows, Environment, useGLTF, OrbitControls, MeshWobbleMaterial } from "@react-three/drei"
import { Canvas, useFrame } from "@react-three/fiber"
import { useSpring, animated, config } from "@react-spring/three";
import Model from './LampRoom3'
import Room from "./LampRoom3 copy";
import { DirectionalLight } from "three";

function Box(props) {
  const mesh = useRef()
  const [hovered, setHover] = useState(false)
  const [active, setActive] = useState(false)
  const [randArgs, setRandArgs] = useState(0.5)

  const { scale } = useSpring({
    scale: active ? Math.random() + 0.2 : Math.random() + 0.2,
    config: config.wobbly
  });

  useFrame(({clock, camera}, delta) => {
    mesh.current.rotation.y = clock.getElapsedTime()
    mesh.current.rotation.x = clock.getElapsedTime()
    mesh.current.rotation.z = clock.getElapsedTime()
  })
  return (
    <animated.mesh
      position={props.position}
      scale={scale}
      onClick={() => setActive(!active)}
      ref={mesh}
    >
      <points>
        <sphereGeometry args={[1, 30, 30]} />
        <pointsMaterial size={0.005} color="#9340ff" sizeAttenuation />
      </points>
    </animated.mesh>
  );
}

